import helper from '../../common/helper.js';
export default {
	data() {
		return {
			fenleiArray: {}, //分类列表菜单
			isActive: "1", //分类列表初始选中状态
			rwlistArray: [{
					ID: 0,
					TaskName: "小龙坎美团五星好评送五折卷",
					PeopleMin: 1,
					PeopleMax: 2,
					Days: 5,
					Surplus: 21
				},
				{
					ID: 1,
					TaskName: "西贝美团五星好评送五折卷",
					PeopleMin: 1,
					PeopleMax: 2,
					Days: 5,
					Surplus: 21
				},
				{
					ID: 2,
					TaskName: "小龙坎美团五星好评送五折卷",
					PeopleMin: 1,
					PeopleMax: 2,
					Days: 5,
					Surplus: 21
				}
			],
			//任务列表数据
			num: '9',        //默认显示9条任务数据
			bottomtisi:''
		}
	},
	onLoad() {
		// this.fenleilists()
	},
	onShow(){
		// this.fenleilists()
	},
	onPullDownRefresh: function() { //下拉刷新
		this.fenleilists(true);
		uni.showLoading({
			title: "刷新中",
			mask: true
		})
	},
	//触底事件
	onReachBottom() {                     
		var tas1 = new String('5');         //触底加载5条数据
		this.num = Number(this.num) + Number(tas1);
		this.fenleilists();
	},
	methods: {
		// 获取任务的id
		openrwid(e) {
			uni.navigateTo({
				url: '../task/Taskdetails?rwid=' + e.currentTarget.dataset.rwid,
			});
		},
		fenleilists(isdown) {
			helper.islogin(true);
			uni.showLoading({
				title: "加载中.....",
				mask: true
			})
			uni.request({
				url: helper.websiteUrl + 'task/gettasklist',
				method: 'GET',
				data: helper.postdata({
					"page": "1",
					"num": this.num,
					"category":"1",
					"userid": helper.getstate().userid,
				}),
				success: (res) => {
					if (res.data.code == 200) {
						this.rwlistArray = res.data.data.baseinfo
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					if (res.data.data.baseinfo.length>=this.num) {
						this.bottomtisi = '正在加载...'
					} else{
						this.bottomtisi = '没有更多数据了'
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		}

	}
}
